import React, { useState } from 'react';
import { Button, Carousel, Col, Container, Row } from 'react-bootstrap';
import one from '../../../images/one.jpg'
import two from '../../../images/two.png'

function Corousel({scrollToContact}) { 
    const [index, setIndex] = useState(0);

    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    return(
        <>
            <Carousel activeIndex={index} onSelect={handleSelect}>
                <Carousel.Item>
                    <Container fluid >
                        <Row>
                            <Col md={6} className="d-flex justify-content-center">
                                <img
                                className="bannerImageWidth"
                                src={one}
                                alt="First slide"
                                />
                            </Col>
                            <Col md={6} className="text-center">
                                <h1 className="brownColor mt-2 openSansSemiBold">Dry Coconuts</h1>
                                <h5 className="pt-3 openSansRegular ">We are one of the leading exporters of dry coconuts</h5>
                                <h6 className="pt-2 openSansRegular content">We are offering fine quality Coconut Copra. We process these coconuts under hygienic conditions using advanced processing methods. With a group of gifted specialists, we are giving a decent quality collection of Natural Copra, The quality group utilizes the best testing ways to deal with check the boundaries of the item's quality.</h6>
                                <Button variant="outline-dark" onClick={scrollToContact} className="m-1 openSansSemiBold">Get a Quote Now</Button>
                            </Col>
                        </Row>
                    </Container>

                </Carousel.Item>
                <Carousel.Item>
                    <Container fluid >
                        <Row>
                            <Col md={6} className="d-flex justify-content-center">
                                <img
                                className="bannerImageWidth p-3"
                                src={two}
                                alt="First slide"
                                />
                            </Col>
                            <Col md={6} className="text-center p-3">
                                <h1 className="brownColor mt-2 openSansSemiBold">Copra's at Best Price</h1>
                                <h5 className="pt-3 openSansRegular ">Wide varieties of Copra's supplied all over india and abroad .</h5>
                                <h6 className="pt-2 openSansRegular content">As we are one of the leading supplier's of coconut products, We have a pricing solutions that deals with key requirement challenges . We know It is a crucial time to ensure our pricing captures the value being delivered contact us today to discuss solutions . </h6>
                                <Button variant="outline-dark" onClick={scrollToContact} className="m-1 openSansSemiBold">Get a Quote Now</Button>
                            </Col>
                        </Row>
                    </Container>
                </Carousel.Item>
            </Carousel>
        </>
    )
}
export default Corousel;